import React, { Component } from "react";
import $ from "jquery";
import "./Sermons.scss";
import AudioComponent from './AudioComponent/AudioComponent';


const audio = document.getElementById("audio-player");
var Waves = document.getElementById("wave");
class Sermons extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isVideoPodcast: true
    };
  }

  changePodcast = () => {
    this.setState({ isVideoPodcast: this.state.isVideoPodcast? false : true });
  };

  componentWillMount() {
    var audio = document.getElementById("audio-player");

    $(document).ready(function() {
      $("#play-button").click(function() {
        if ($(this).hasClass("unchecked")) {
          $(this)
            .addClass("play-active")
            .removeClass("play-inactive")
            .removeClass("unchecked");
          $(".info-two").addClass("info-active");
          $("#pause-button").addClass("scale-animation-active");
          $(
            ".waves-animation-one, #pause-button, .seek-field, .volume-icon, .volume-field, .info-two"
          ).show();
          $(".waves-animation-two").hide();
          $("#pause-button")
            .children(".icon")
            .addClass("icon-pause")
            .removeClass("icon-play");
          setTimeout(function() {
            $(".info-one").hide();
          }, 400);
          audio.play();
          audio.currentTime = 0;
        } else {
          $(this)
            .removeClass("play-active")
            .addClass("play-inactive")
            .addClass("unchecked");
          $("#pause-button")
            .children(".icon")
            .addClass("icon-pause")
            .removeClass("icon-play");
          $(".info-two").removeClass("info-active");
          $(
            ".waves-animation-one, #pause-button, .seek-field, .volume-icon, .volume-field, .info-two"
          ).hide();
          $(".waves-animation-two").show();
          setTimeout(function() {
            $(".info-one").show();
          }, 150);
          audio.pause();
          audio.currentTime = 0;
        }
      });
      $("#pause-button").click(function() {
        $(this)
          .children(".icon")
          .toggleClass("icon-pause")
          .toggleClass("icon-play");

        if (audio.paused) {
          audio.play();
        } else {
          audio.pause();
        }
      });
      $("#play-button").click(function() {
        setTimeout(function() {
          $("#play-button")
            .children(".icon")
            .toggleClass("icon-play")
            .toggleClass("icon-cancel");
        }, 350);
      });
      $(".like").click(function() {
        $(".icon-heart").toggleClass("like-active");
      });
    });

    function CreateSeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      seekbar.min = 0;
      seekbar.max = audio.duration;
      seekbar.value = 0;
    }

    function EndofAudio() {
      document.getElementById("audioSeekBar").value = 0;
    }

    function audioSeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      audio.currentTime = seekbar.value;
    }

    function SeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      seekbar.value = audio.currentTime;
    }

    window.onload = function() {
      audio.addEventListener(
        "timeupdate",
        function() {
          var duration = document.getElementById("duration");
          var s = parseInt(audio.currentTime % 60);
          var m = parseInt((audio.currentTime / 60) % 60);
          duration.innerHTML = m + ":" + s;
        },
        false
      );

      Waves.init();
      Waves.attach("#play-button", ["waves-button", "waves-float"]);
      Waves.attach("#pause-button", ["waves-button", "waves-float"]);
    };
  }

  componentDidMount() {
    var audio = document.getElementById("audio-player");

    $(document).ready(function() {
      $("#play-button").click(function() {
        if ($(this).hasClass("unchecked")) {
          $(this)
            .addClass("play-active")
            .removeClass("play-inactive")
            .removeClass("unchecked");
          $(".info-two").addClass("info-active");
          $("#pause-button").addClass("scale-animation-active");
          $(
            ".waves-animation-one, #pause-button, .seek-field, .volume-icon, .volume-field, .info-two"
          ).show();
          $(".waves-animation-two").hide();
          $("#pause-button")
            .children(".icon")
            .addClass("icon-pause")
            .removeClass("icon-play");
          setTimeout(function() {
            $(".info-one").hide();
          }, 400);
          audio.play();
          audio.currentTime = 0;
        } else {
          $(this)
            .removeClass("play-active")
            .addClass("play-inactive")
            .addClass("unchecked");
          $("#pause-button")
            .children(".icon")
            .addClass("icon-pause")
            .removeClass("icon-play");
          $(".info-two").removeClass("info-active");
          $(
            ".waves-animation-one, #pause-button, .seek-field, .volume-icon, .volume-field, .info-two"
          ).hide();
          $(".waves-animation-two").show();
          setTimeout(function() {
            $(".info-one").show();
          }, 150);
          audio.pause();
          audio.currentTime = 0;
        }
      });
      $("#pause-button").click(function() {
        $(this)
          .children(".icon")
          .toggleClass("icon-pause")
          .toggleClass("icon-play");

        if (audio.paused) {
          audio.play();
        } else {
          audio.pause();
        }
      });
      $("#play-button").click(function() {
        setTimeout(function() {
          $("#play-button")
            .children(".icon")
            .toggleClass("icon-play")
            .toggleClass("icon-cancel");
        }, 350);
      });
      $(".like").click(function() {
        $(".icon-heart").toggleClass("like-active");
      });
    });

    function CreateSeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      seekbar.min = 0;
      seekbar.max = audio.duration;
      seekbar.value = 0;
    }

    function EndofAudio() {
      document.getElementById("audioSeekBar").value = 0;
    }

    function audioSeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      audio.currentTime = seekbar.value;
    }

    function SeekBar() {
      var seekbar = document.getElementById("audioSeekBar");
      seekbar.value = audio.currentTime;
    }

    window.onload = function() {
   
    };
  }

  render() {
    window.onload = function() {
      audio.addEventListener(
        "timeupdate",
        function() {
          var duration = document.getElementById("duration");
          var s = parseInt(audio.currentTime % 60);
          var m = parseInt((audio.currentTime / 60) % 60);
          duration.innerHTML = m + ":" + s;
        },
        false
      );

      $(document).ready(function() {
        Waves.init();
        Waves.attach("#play-button", ["waves-button", "waves-float"]);
        Waves.attach("#pause-button", ["waves-button", "waves-float"]);
      });
    };

    let {isVideoPodcast} =this.state;
    let namePodcast = isVideoPodcast ? "Audio Podcast" : "Video Podcast";
    return (
      <div className={"sermon"}>
        <button
          className={"button-podcast"}
          onClick={() =>
           this.changePodcast()
            //console.log("This is onclick")
          }
        >
          {namePodcast}
        </button>
        {isVideoPodcast ? (
          <div className={"sermon"}>
        
            <div className={"video-podcast"}>
              <iframe className={"video-podcast"} frameborder="0" src="https://www.youtube.com/embed/vTN8QS1Bb-Q" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture; allowfullscreen" allowfullscreen>
              </iframe> 
            </div>
            {/* <AudioComponent/> */}
          </div>
        ) : (
          !isVideoPodcast && <div className={"container"}>
            <div className={"player"}>
              <div className={"like waves-effect waves-light"}>
                <i className={"icon-heart"} />
              </div>
              <div className={"mask"} />

              <ul className={"player-info info-one"}>
                <li>Apostle N. Mcoteli</li>
                <li>Session 1 - Harvest</li>
                <li>29:06</li>
              </ul>

              <ul className={"player-info info-two"}>
                <li>Apostle N. Mcoteli</li>
                <li>Session 1 - Harvest</li>
                <li>
                  <span id="duration"></span>
                  <i> / </i>29:06
                </li>
              </ul>

              <div id="play-button" className={"unchecked"}>
                <i className={"icon icon-play"}></i>
              </div>

              <div className={"control-row"}>
                <div className={"waves-animation-one"} />
                <div className={"waves-animation-two"} />
                <div className={"#pause-button"}>
                  <i className={"icon"} />
                </div>
                <div className={"seek-field"}>
                  <input
                    className={"#audioSeekBar"}
                    min="0"
                    max="334"
                    step="1"
                    value="0"
                    type="range"
                    oninput="audioSeekBar()"
                    onchange="this.oninput()"
                  />
                </div>
                <div className={"volume-icon"}>
                  <i className={"icon-volume-up"} />
                </div>
                <div className={"volume-field"}>
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value="100"
                    step="1"
                    oninput="audio.volume = this.value/100"
                    onchange="this.oninput()"
                  />
                </div>
              </div>
            </div>
            <audio
              id="audio-player"
              ontimeupdate="SeekBar()"
              ondurationchange="CreateSeekBar()"
              preload="auto"
              loop
            >
              <source
                src="https://michael-zhigulin.github.io/mz-codepen-projects/Material%20Design%20UI%20Audio%20Player/audio/Tim%20Minchin%20%E2%80%94%20Rock%20n%20Roll%20Nerd.ogg"
                type="audio/ogg"
              />
              <source
                src="https://michael-zhigulin.github.io/mz-codepen-projects/Material%20Design%20UI%20Audio%20Player/audio/Tim%20Minchin%20%E2%80%94%20Rock%20n%20Roll%20Nerd.mp3"
                type="audio/mpeg"
              />
            </audio>
          </div>
        )}
      </div>
    );
  }
}

export default Sermons;
