import React, { Component } from "react";
import "./AudioComponent.scss";

const videoTrack = document.getElementById("videoTrack");
var videoSound = document.getElementById("videoSound");
const videoPlayer = document.getElementById("videoPlayer");
var soundTrack = document.getElementById("soundTrack");
const videoPlay = document.getElementById("videoPlay");
var videoPause = document.getElementById("videoPause");
var videoEnd = document.getElementById("videoEnd");
var videoStart = document.getElementById("videoStart");
var videoTime = document.getElementById("videoTime");
var videoFullScreen = document.getElementById("videoFullScreen");
var videoSoundIcon = document.getElementById("videoSoundIcon");

class AudioComponent extends Component {
  componentDidMount() {
   
  }

  componentWillMount() {
    var videoPlayer = document.getElementById("video__player"),
      videoDuration = 0,
      actualMoment = 0,
      updateVolume;

    videoPlayer.addEventListener("loadedmetadata", function() {
      videoDuration = Math.round(videoPlayer.duration);
      videoTrack.setAttribute("max", videoDuration);
    });

    videoSound.addEventListener("click", function() {
      if (soundTrack.value == 0) {
        controlVolumeStatus(1);
        soundTrack.value = 10;
      } else {
        controlVolumeStatus(0);
        soundTrack.value = 0;
      }
    });

    videoPlay.addEventListener("click", function() {
      videoPlayer.play();
    });
    videoPause.addEventListener("click", function() {
      videoPlayer.pause();
    });
    videoStart.addEventListener("click", function() {
      videoPlayer.pause();
      videoPlayer.currentTime = 0;
      videoTrack.value = 0;
    });
    videoEnd.addEventListener("click", function() {
      videoPlayer.pause();
      videoPlayer.currentTime = videoDuration - 1;
      videoTrack.value = videoDuration - 1;
    });
    videoFullScreen.addEventListener("click", function() {
      if (videoPlayer.requestFullscreen) {
        videoPlayer.requestFullscreen();
      } else if (videoPlayer.mozRequestFullScreen) {
        videoPlayer.mozRequestFullScreen(); // Firefox
      } else if (videoPlayer.webkitRequestFullscreen) {
        videoPlayer.webkitRequestFullscreen(); // Chrome and Safari
      }
    });

    videoTrack.addEventListener("change", function() {
      videoPlayer.currentTime = this.value;
    });

    updateVolume = function() {
      window.requestAnimationFrame(function() {
        controlVolumeStatus(soundTrack.value / 10);
      });
    };

    soundTrack.addEventListener("mousedown", function() {
      updateVolume();
      soundTrack.addEventListener("mousemove", updateVolume);
    });
    soundTrack.addEventListener("mouseup", function() {
      soundTrack.removeEventListener("mousemove", updateVolume);
    });

    videoPlayer.addEventListener("timeupdate", function() {
      videoTrack.value = videoPlayer.currentTime;
      videoTime.innerHTML = controlTimeFormat(videoPlayer.currentTime);
      if (Math.round(videoPlayer.currentTime) === videoDuration) {
        setTimeout(function() {
          videoPlayer.currentTime = 0;
          videoTrack.value = 0;
          videoPlayer.pause();
        }, 1000);
      }
    });

    function controlTimeFormat(ms) {
      var hr = Math.floor(ms / 3600),
        min = Math.floor((ms - hr * 3600) / 60),
        sec = Math.floor(ms - hr * 3600 - min * 60);

      if (min < 10) {
        min = "0" + min;
      }
      if (sec < 10) {
        sec = "0" + sec;
      }
      if (hr < 10) {
        hr = "0" + hr;
      }

      return hr + ":" + min + ":" + sec;
    }

    function controlVolumeStatus(val) {
      videoPlayer.volume = val;
      console.log(videoPlayer.volume);
      if (val === 0) {
        videoSoundIcon.setAttribute("xlink:href", "#i-mute");
      } else {
        videoSoundIcon.setAttribute("xlink:href", "#i-volume");
      }
    }

    setTimeout(function() {
      videoPlayer.pause();
    }, 500);
  }

  render() {

    return (
      <div className={"audio-component"}>
        <main class="flexy__item flexy--items-center flexy--justify-center flexy--column">
          <section class="section flexy__item flexy--column flexy--items-center space--big">
            
            <article class="section__article flexy__item flexy--column flexy--items-center">
              
              <div class="video flexy__item">
                <video
                  class="video__player"
                  id="video__player"
                  autoplay="autoplay"
                  preload="metadata"
                >
                  <source
                    src="http://distribution.bbb3d.renderfarming.net/video/mp4/bbb_sunflower_2160p_60fps_normal.mp4"
                    type="video/mp4"
                  />
                </video>

                <div class="video__control flexy__item">
                  <div
                    class="video__duration flexy__item flexy--items-center tooltip"
                    id="video__duration"
                    data-tooltip-content="Duration"
                  >
                    00:00:00
                  </div>
                  <ul class="flexy__item">
                    <li
                      class="video__control__button tooltip"
                      id="video__start"
                      data-tooltip-content="Start"
                    ></li>

                    <li
                      class="video__control__button tooltip"
                      id="video__play"
                      data-tooltip-content="Play"
                    ></li>

                    <li
                      class="video__control__button tooltip"
                      id="video__pause"
                      data-tooltip-content="Pause"
                    ></li>

                    <li
                      class="video__control__button tooltip"
                      id="video__end"
                      data-tooltip-content="End"
                    ></li>

                  </ul>
                  <div
                    class="video__control__button video__track flexy__child flexy__item flexy--items-center tooltip"
                    data-tooltip-content="Track"
                  >
                    <input
                      class="video__track__range flexy__child"
                      id="video__track"
                      type="range"
                      step="1"
                      min="0"
                      max="0"
                      value="0"
                    />
                  </div>
                  <div
                    class="video__control__button volume flexy__item tooltip"
                    data-tooltip-content="Volume"
                  >
                    <div class="video__track__range__sound">
                      <input
                        class="video__track__range"
                        id="sound__track"
                        type="range"
                        step="1"
                        min="0"
                        max="10"
                        value="10"
                      />
                    </div>
                  </div>
                  <div
                    class="video__control__button tooltip"
                    id="video__fullScreen"
                    data-tooltip-content="Maximize"
                  ></div>
                </div>
              </div>
            </article>
          </section>
          <header class="header">
            <h1>
              <a href="http://tadaima.studio" title="Go to Tadaima"></a>
            </h1>
          </header>
        </main>
      </div>
    );
  }
}

export default AudioComponent;
