import React, { Component } from 'react';
import LoginForm from '../Login/Login';
import Welcome from '../Home/Home';
import './LandingPage.scss';

class LandingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null
    };
  }

  cornerAnimation = () =>{
    return <div classNameName={'base'}>
            <div className={'menu'}>
                <div className={'icon'}>
                <div className={'bar'}></div>
                </div>
            </div>
            <div className={'icons'}>
                <i className='fa fa-user' aria-hidden='true'></i>
                <i className='fa fa-calendar-o' aria-hidden='true'></i>
                <i className='fa fa-tachometer' aria-hidden='true'></i>
            </div>
            <div className={'section'}>
                <div className={'cover1'}>
                <div className={'cover2'}>
                    <a className={'content'} href='#calender'></a>
                </div>
                </div>
            </div>
            <a className={'section-static top'} href='#profile'></a>
            <a className={'section-static bottom'} href='#dashboard'></a>
    </div>
  }
  render() {
    return (
        <div classNameName={'landing-page-container'}>
            {this.cornerAnimation()}
            LandingPage
        </div>
    );
  }
}

export default LandingPage;
