import React, { Component } from 'react'
import { Switch, Route, Redirect} from 'react-router-dom';
import Header from './components/Header/Header';
import LoginComponent from './components/Login/Login';
import BishopComponent from './components/Bishop/Bishop';
import AdminComponent from './components/Admin/Admin';
import DisciplerComponent from './components/Discipler/MyDisciple';
import HomeComponent from './components/Home/Home';
import AboutComponent from './components/About/About';
import EventsComponent from './components/Events/Events';
import SermonsComponent from './components/Sermons/Sermons';
import MinistriesComponents from './components/Ministries/Ministries';
import Registration from './components/Registration/Registration';
import CalendarComponent from './components/Calendar/Calendar';

import { HOME, ABOUT,EVENTS, SERMONS,MINISTRIES, REGISTRATION, LOGIN, DISCIPLER, ADMIN, BISHOP, CALENDAR} from './components/constants/pageUrls';

class routes extends Component {
    render() {
        return (
            <div>
                 <Switch>
                    <Route exact path={HOME}>
                        <HomeComponent/> 
                    </Route>
                    <Route exact path={ABOUT}>
                        <Header/>
                        <AboutComponent />                       
                    </Route>
                    <Route exact path={EVENTS}>
                        <Header/>
                        <EventsComponent />                       
                    </Route>
                    <Route exact path={SERMONS}>
                        <Header/>
                        <SermonsComponent />                       
                    </Route>
                    <Route exact path={MINISTRIES}>
                        <Header/>
                        <MinistriesComponents />                       
                    </Route>
                    <Route exact path={REGISTRATION}>
                        <Header/>
                        <Registration />                       
                    </Route>
                    <Route exact path={LOGIN}>
                        <LoginComponent />                       
                    </Route>
                    <Route exact path={DISCIPLER}>
                        <Header/>
                        <DisciplerComponent />                       
                    </Route>
                    <Route exact path={BISHOP}>
                        <Header/>
                        <BishopComponent />                       
                    </Route>
                    <Route exact path={ADMIN}>
                        <Header/>
                        <AdminComponent />                       
                    </Route>
                    <Route exact path={CALENDAR}>
                        <CalendarComponent />                       
                    </Route>
                    <Redirect to={'/'} />
                 </Switch>
            </div>
        )
    }
}


export default routes;